
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default{
data(){
return {
itegal:{rights:'',title:'积分审核'},
topShow:false,

slides:-1,
parks:'',
partyBranch1:'',
department:'',
list1:'',
list2:'',
list3:'',
table1:true,
departmentid:'',
length1:'',
length2:'',
partyBranch:'',
activesId:1
}
},
components:{
XHeader,
ViewBox,
Panel,
Sticky,
Flexbox,
FlexboxItem,
Tab,
TabItem
},
directives: {
TransferDom
},
handlePicker1(){
this.PickerVisible5=true;
axios({
method: 'get',
url: 'pdepartment/getList'
}) .then((res)=> {
this.list1= res.data;
}).catch(function (error) {
console.log(error);
});
},
methods:{
getlist(){
axios({
method: 'get',
url: 'pdepartment/getList',
}) .then((res)=> {
this.department=res.data;
this.partyBranch1 = this.department[0].departmentname;
this.departmentid= this.department[0].departmentid;
this.slide();
axios({
method: 'get',
url: 'ppartymember/getPartymemberByDepartmentid',
params: {
departmentid:this.departmentid,
status:1
}
}).then((res)=> {
this.length2=res.data.length;
}).catch(function (error) {
console.log(error);
});
}).catch(function (error) {
console.log(error);
})
},
showDet(){
this.topShow = !this.topShow;

},
slide(){
this.table1 = true;
axios({
method: 'get',
url: 'ppartymember/getPartymemberByDepartmentid',
params: {
departmentid:this.departmentid,
status:0
}
}) .then((res)=> {
this.list1=res.data;
this.length1=res.data.length;
}).catch(function (error) {
console.log(error);
});
},
slide1(){
this.table1 = false;
axios({
method: 'get',
url: 'ppartymember/getPartymemberByDepartmentid',
params: {
departmentid:this.departmentid,
status:1
}
}).then((res)=> {
this.list2=res.data;
this.length2=res.data.length;
}).catch(function (error) {
console.log(error);
});
},
change(park){
let temp = this.table1;
this.activesId = park.departmentid;
this.partyBranch1 = park.departmentname;
this.departmentid = park.departmentid;
this.partyBranch = park.partyBranch;

this.topShow = !this.topShow;
this.showTrans = !this.showTrans;
this.slide1();
this.slide();
this.table1 = temp;
},
gohandle(item){
this.$router.push({
path: '/points/audit',
name: 'Audit',
params: {
partmentId:item.id,
name:encodeURI(item.name),
departmentId:item.departmentid,
userId:item.userid
}
});
}
},
mounted(){
this.getlist()
}
}



